
.mainbranches-aboutus-tabs{
    display: flex;
    justify-content: space-between;
    /*margin-top: 5%;*/
}

.mainbranches-aboutus-tabs .button {
    padding: 15px;
    text-align: center;
    background-color: #3b7a4e;
    color: #ece7e7;
    width: 33%;
    text-decoration: none;
    border: 1px solid #3b7a4e;
}


.mainbranches-aboutus-tabs .button:hover{
    background-color: #5c5e5c;
    box-shadow: 5px 5px 10px #c5c3c3;
    border: none;
    cursor: pointer;
    transition: 0.5s;
}

.mainbranches-aboutus-tabs .tab-clicked {
    padding: 15px;
    text-align: center;
    background-color: #3b7a4e;
    color: #ece7e7;
    width: 33%;
    text-decoration: none;
    border: 1px solid #3b7a4e;
}

.mainbranches-aboutus-tabs .tab-clicked{
    background-color: #242524;
    box-shadow: 5px 5px 10px #c5c3c3;
    border: none;
    cursor: pointer;
    transition: 0.5s;
}

.hide-tab-div{
    display: none;
}

.show-tab-div{
    padding: 20px;
    margin-top: 1%;
}

.aboutus-buy-grid a{
    text-decoration: none;
    line-height: 25px;
    font-size: 17px;
}


.aboutus-buy-grid{
    display: grid;
    grid-template-areas: "pic text";
    grid-template-columns: 1fr 2fr;
    grid-template-rows: 1fr;
    align-items: center;
    justify-items: center;
    grid-column-gap: 3%;
    background-color: white;
}

.aboutus-img{
    grid-area: pic;
}

.aboutus-img .img-in-aboutus{
    width: 100%;

}

.aboutus-img .img-in-aboutus:hover{
    transform: scale(1);
}

