.aboutus-title h2{
    position: relative;
    left: -500px;
    top: 250px;
    font-size: 40px;
    color: #21462c;
    margin: 0;
    text-align: right;
}

.aboutus-grid-text{
    /*padding: 1% 0;*/
}

.aboutus-grid-text p{
    /*margin: 0 2% 2% 0;*/
    text-align: justify;
}

.mainbranches-aboutus-wrapper a{
    text-decoration: none;
    line-height: 29px;
}

.aboutus-grid-button{
    padding: 15px;
    text-align: center;
    background-color: #3b7a4e;
    color: #ece7e7;
    width: 33%;
    border: none;
    border-radius: 8px;
}


.aboutus-grid-button:hover{
    background-color: #242524;
    box-shadow: 5px 5px 10px #c5c3c3;
    border: none;
    cursor: pointer;
    transition: 0.5s;
}
